@mixin on-tablet {
  @media (min-width: 576px) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width){
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
    margin-inline: auto;
  }

  @include on-desktop {
    max-width: 1260px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}

@mixin contacts-sizing {
  &:hover {
    font-weight: 500;
    color: $main-input-hover;
  }

  box-sizing: border-box;
  outline: none;
  font-family: Poppins, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  border: 0;
  width: 100%;
  padding: 14px 16px;
}
